.hero_background{
  // background-color: beige;
  margin-left: auto;
  margin-right: auto;
  max-width: 375px;
  height: 734px;
  margin-bottom: 80px;
  background-image: url(../images/hero-pic-mob.jpeg);
  background-repeat: no-repeat;
  background-position: right;
  background-size: contain;

  @media screen and (min-width: 768px){
    max-width: 768px;
    height: 917px;
    margin-bottom: 100px;
    background-image: url(../images/hero-pic-tablet.jpeg);
    background-repeat: no-repeat;
    background-position: right;
    background-size: contain;
  }

  @media screen and (min-width: 1440px) {
    max-width: 1600px;
    height: 706px;
    background-image: url(../images/hero-pic.jpeg);
    background-repeat: no-repeat;
    background-position: right;
    background-size: contain;
  }



}

.hero{
  max-width: 279px;
  padding-top: 128px;
  padding-bottom: 318px;

    @media screen and (min-width: 768px){
      max-width: 483px;
      padding-top: 184px;
      padding-bottom: 359px;
    }

    @media screen and (min-width: 1440px) {
      max-width: 715px;
      padding-top: 160px;
      padding-bottom: 216px;
    }


  
}

.hero_title{
  font-size: 32px;
  margin-bottom: 16px;

  @media screen and (min-width: 768px){
    font-size: 56px;
    margin-bottom: 28px;
  }

  @media screen and (min-width: 1440px){
    font-size: 64px;
    margin-bottom: 28px;
  }

}

.hero_text{
  font-size: 14px;
  margin-bottom: 32px;

  @media screen and (min-width: 768px){
    font-size: 18px;
    margin-bottom: 40px;
  }

  @media screen and (min-width: 1440px){
    font-size: 18px;
    margin-bottom: 40px;
  }

}